":["@import \"../abstracts/colours\";\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');\n\nbody {\n  font-family: 'Roboto', sans-serif;\n  font-size: 20px;\n}\n\n@mixin monospace {\n  font-family: 'Roboto Mono', sans-serif;\n}\n","@import \"../abstracts/colours\";\n\nbody {\n  background-color: $outer-background;\n  color: $foreground;\n  display: flex;\n  flex-direction: column;\n  width: 98%;\n  height: 98vh;\n  margin: 1vh 1vw;\n}\n\ninput, button, select {\n  background-color: $current-line;\n  color: $foreground;\n  border: $input-outline solid 1px;\n  border-radius: 3px;\n  padding: 0 2px;\n  font-family: 'Roboto Mono', sans-serif;\n\n  &:disabled {\n    background-color: $disabled-input;\n    color: $disabled-input-text;\n    cursor: not-allowed;\n  }\n}\n\nbutton {\n  font-family: 'Roboto', sans-serif;\n\n  &:active {\n    background-color: $highlighted-tree;\n  }\n\n  &:not(:disabled) {\n    cursor: pointer;\n  }\n}\n","// Color scheme\n$tree-background: #282a36\n$outer-background: #21222c\n$semi-background: #242530\n$current-line: #44475a\n$foreground: #f8f8f2\n$comment: #6272a4\n$cyan: #8be9fd\n$green: #50fa7b\n$orange: #ffb86c\n$pink: #ff79c6\n$purple: #bd93f9\n$red: #ff5555\n$yellow: #f1fa8c\n\n// Additional UI colors\n$context-menu-background: $current-line\n$highlighted-tree: #52566c\n$input-outline: #14141a\n$disabled-input: #3c3f4e\n$disabled-input-text: #b6b7b6\n$highlighted-input: #0d3806\n$highlighted-input-text: $foreground\n\n// Value colors\n$value-colour: $green\n$value-type-colour: $yellow\n","button {\n  background-color: $current-line;\n  color: $foreground;\n  border: $input-outline solid 2px;\n  border-radius: 3px;\n  padding: 2px;\n  font-family: 'Roboto', sans-serif;\n\n  &:disabled {\n    background-color: $disabled-input;\n    color: $disabled-input-text;\n  }\n\n  &:active {\n    background-color: $highlighted-tree;\n  }\n\n  &:not(:disabled) {\n    cursor: pointer;\n  }\n}\n",".custom-menu {\n  display: none;\n  position: absolute;\n  box-shadow: 1px 1px 2px #888;\n  border: solid 1px #ccc;\n  background-color: $context-menu-background;\n  padding: 2px;\n  z-index: 1000;\n}\n\n#custom-context-menu ul {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n\n  li {\n    padding: 10px;\n    cursor: pointer;\n    text-align: left;\n\n    &:hover {\n      background-color: $highlighted-tree;\n    }\n  }\n}\n","@import '../abstracts/colours';\n\n.error-message {\n  position: fixed;\n  bottom: 20px;\n  right: 20px;\n  background-color: $red;\n  color: $tree-background;\n  padding: 15px;\n  border-radius: 5px;\n  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);\n  opacity: 0;\n  visibility: hidden;\n  transition: opacity 0.5s, visibility 0.5s;\n\n  &.fade-in {\n    opacity: 1;\n    visibility: visible;\n  }\n\n  &.fade-out {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n","div.expr-selector-container {\n  display: inline-flex;\n  position: relative;\n\n  &.guide-highlight * {\n    background-color: $highlighted-tree;\n    color: $highlighted-input-text;\n  }\n\n  .expr-selector-input {\n    margin: 0;\n    border-radius: 3px 0 0 3px;\n  }\n\n  .expr-selector-button {\n    margin: 0;\n    border: $input-outline solid 1px;\n    border-radius: 0 3px 3px 0;\n  }\n\n  .expr-selector-dropdown {\n    position: absolute;\n    top: 100%;\n    left: 0;\n    background-color: $context-menu-background;\n    color: $foreground;\n    border: $input-outline solid 1px;\n    z-index: 1;\n\n    > ul {\n      list-style: none;\n      padding: 0;\n      margin: 0;\n\n      .expr-selector-option {\n        cursor: pointer;\n        padding: 5px;\n        text-align: left;\n        z-index: 100;\n\n        &.highlight {\n          background-color: $highlighted-tree;\n        }\n      }\n    }\n  }\n}\n","form {\n  display: inline;\n}\n\ninput {\n  background-color: $current-line;\n  color: $foreground;\n  border: $input-outline solid 1px;\n  border-radius: 3px;\n  padding: 0 2px;\n  @include monospace;\n\n  &:disabled {\n    background-color: $disabled-input;\n    color: $disabled-input-text;\n    cursor: not-allowed;\n  }\n}\n\nselect {\n  background-color: $current-line;\n  color: $foreground;\n  border: $input-outline solid 1px;\n  border-radius: 3px;\n  padding: 0 2px;\n\n  &:disabled {\n    background-color: $disabled-input;\n    color: $disabled-input-text;\n    cursor: not-allowed;\n    opacity: 0.6;\n  }\n}\n\nlabel, .prevent-select, #tree :not(input) {\n  user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n}\n","#controls {\n  display: flex;\n  margin-bottom: 10px;\n\n  > div {\n    margin: 0 10px 0 0;\n    background-color: $semi-background;\n    border: 2px solid $input-outline;\n    border-radius: 2px;\n    padding: 2px;\n  }\n\n  h3 {\n    margin-top: 0;\n  }\n\n  h4 {\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n}\n","@import \"../abstracts/colours\";\n\n#tree-container {\n  border: $foreground solid 1px;\n  background-color: $tree-background;\n  width: 100%;\n  height: 100%;\n  min-height: 100px;\n  overflow: hidden;\n\n  &.file-drag-highlight {\n    border: 2px dashed #4CAF50;\n    background-color: #49494d;\n  }\n}\n\n#tree {\n  max-width: fit-content;\n  overflow: visible;\n  white-space: nowrap;\n}\n\n#tree, .subtree {\n  display: flex;\n  flex-direction: column-reverse;\n  align-items: center;\n  align-self: flex-end;\n  position: relative;\n  border-radius: 3px;\n}\n\n.highlight {\n  background-color: $highlighted-tree;\n}\n\n.args {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n}\n\n.node {\n  display: inline-flex;\n  margin-top: 0.5em;\n  align-items: center;\n  justify-content: space-between;\n\n  div:not(.expr-selector-container) {\n    display: inline;\n  }\n}\n\ndiv.scoped-variables {\n  display: inline-flex;\n}\n\n.annotation-axiom {\n  align-self: end;\n  font-size: 12px;\n  color: $comment;\n}\n\n.annotation-new {\n  font-size: 12px;\n  color: $comment;\n  align-self: flex-end;\n}\n\n.axiom {\n  display: flex;\n  flex-direction: column-reverse;\n}\n\n.subtree:not(:last-child) {\n  margin-right: 20px;\n}\n\n.subtree::before {\n  content: '';\n  height: 2px;\n  background: $foreground;\n  width: 100%;\n  position: absolute;\n  bottom: 1.4em;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.tooltip:not(.tooltip .tooltip) {\n  position: relative;\n  display: inline-block;\n  border-bottom: 1px dotted $foreground;\n}\n\n.tooltip .tooltip-text {\n  visibility: hidden;\n  background-color: $context-menu-background;\n  color: $foreground;\n  border: $input-outline solid 1px;\n  text-align: center;\n  border-radius: 6px;\n  padding: 5px 3px;\n  position: absolute;\n  z-index: 1;\n  bottom: 125%;\n  left: 50%;\n  margin-left: -60px;\n  opacity: 0;\n  transition: opacity 0.3s;\n}\n\n.tooltip:hover .tooltip-text {\n  visibility: visible;\n  opacity: 1;\n}\n\n.tooltip div {\n  display: inline;\n}\n\n.value-highlighting-enabled .value {\n  color: $value-colour;\n}\n\n.value-highlighting-enabled .value-type {\n  color: $value-type-colour;\n}\n\n.error-origin {\n  color: $red;\n  @include monospace;\n}\n\n.error-child {\n  color: $orange;\n}\n","#tree-buttons {\n  margin: 3px;\n  z-index: 100;\n  position: relative;\n  display: inline-block;\n\n  button {\n    padding: 0;\n\n    img {\n      vertical-align: middle;\n    }\n  }\n}\n"],"sourceRoot":""}]);
